import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Home/Index'
import NewTodo from '@/components/Newtodo/Index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/NewTodo',
      name: 'NewTodo',
      component: NewTodo
    }
  ]
})



