  },
  created () {},
  mounted () {}
}
</script>
<style lang="scss" scoped>
ul {
  width: 100%;
  li {
    width: 100%;
    display: flex;
    .time-wrapper {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 1.4rem;
      font-size: .48rem;
      color: #FFF;
      text-align: center;
      background: #adacac;
    }
    .text-wrapper {
      flex: 1;
      display: flex;
      color: #FEFEFE;
      flex-direction: column;
      background: #c1afaf;
      padding: .4rem;
      .text {
        font-size: .32rem;
      }
      .date {
        margin-top: .2rem;
      }
    }
    .operation-wrapper {
      width: 1.4rem;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #FFF;
      font-size: .48rem;
    }
  }
}
</style>



