<template>
  <div class="wrapper">
    <div class="new-wrapper">
      <div class="ipt-wrapper">
        <textarea name="" id="" cols="30" rows="10" placeholder="您今天想要做什么...?"></textarea>
      </div>
      <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
      <div class="choose-wrapper" @click="handleHome">
        <span class="fa fa-check-circle-o square"></span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'NewTodo',
  components: {},
  props: {},
  data () {
    return {
      slots: [
        {
          flex: 1,
          values: [],
          className: 'slot1',
          textAlign: 'right'
        }, {
          divider: true,
          content: '-',
          className: 'slot2'
        }, {
          flex: 1,
          values: [],
          className: 'slot3',
          textAlign: 'left'
        }
      ]
    }
  },
  watch: {},
  computed: {},
  methods: {
    onValuesChange (picker, values) {
    },
    getNum () {
      for (let i = 0; i <= 60; i++) {
        if (i < 10) {
          i = '0' + i
        }
        if (i < 24) {
          this.slots[0].values.push(i)
        }
        this.slots[2].values.push(i)
      }
    },
    handleHome () {
      this.$router.push('./')
    }
  },
  created () {
    this.getNum()
  },
  mounted () {}
}
</script>
<style lang="scss" scoped>
.wrapper {
  width: 100%;
  height: 100vh;
  background: #59b159;
  display: flex;
  justify-content: center;
  align-items: center;
  .new-wrapper {
    width: 90%;
    height: 90vh;
    padding: .1rem;
    border-radius: .2rem;
    background: #FFF;
    position: relative;
    .ipt-wrapper {
      width: 80%;
      margin: 0 auto;
      textarea {
        width: 100%;
        border: 0;
        font-size: .48rem;
        background-color:transparent;
        color: #666464;
        height: 50vh;
      }
      textarea::-webkit-input-placeholder {
        color: #59b159;
        font-size: .48rem;
      }
    }
    .change-wrapper {
      width: 100%;
      height: 1rem;
      text-align: center;
    }
    .choose-wrapper {
      position: absolute;
      width: 1.2rem;
      height: 1.2rem;
      line-height: 1.2rem;
      text-align: center;
      border-radius: 1rem;
      background: #2e922e;
      bottom: -.5rem;
      left: 50%;
      margin-left: -.6rem;
      .square {
        font-size: 1.2rem;
        color: #FFF;
      }
    }
  }
}
</style>



