<template>
  <div class="wrapper">
    <home-header :thatTime="thatTime" :thatDate="thatDate"></home-header>
    <home-list></home-list>
    <home-footer></home-footer>
  </div>
</template>

<script>
import HomeHeader from './Header'
import HomeList from './List'
import HomeFooter from './Footer'
export default {
  name: '',
  components: {
    HomeHeader,
    HomeList,
    HomeFooter
  },
  props: {},
  data () {
    return {
      thatTime: '',
      thatDate: ''
    }
  },
  watch: {},
  computed: {},
  methods: {
    timeFormat (date) {
      let hours = date.getHours()
      let min = date.getMinutes()
      let month = date.getMonth()
      if (min < 10) {
        min = '0' + min
      }
      if (month < 10) {
        month = '0' + month
      }
      let thatDate = date.getDate()
      if (thatDate < 10) {
        thatDate = '0' + thatDate
      }
      this.thatDate = month + '/' + thatDate
      this.thatTime = hours + ':' + min
    },
    getNowTimes () {
      let date = new Date()
      this.timeFormat(date)
      setInterval(this.getNowTimes, 20 * 1000)
    }
  },
  created () {
    this.getNowTimes()
  },
  mounted () {
    this.getNowTimes()
  }
}
</script>
<style lang="scss" scoped>
.wrapper {
  position: relative;
}
</style>



