<template>
  <div class="wrapper">
    <div class="time-wrapepr">
      <span>{{thatTime}}</span>
    </div>
    <div class="date-wrapper">
      <span>{{thatDate}}</span>
      <span class="fa fa-calendar calendar"></span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeHeader',
  components: {},
  props: ['thatTime', 'thatDate'],
  data () {
    return {
    }
  },
  watch: {},
  computed: {},
  methods: {},
  created () {},
  mounted () {}
}
</script>
<style lang="scss" scoped>
.wrapper {
  height: 1.4rem;
  line-height: 1.4rem;
  background: #eee;
  position: relative;
  .time-wrapepr {
    width: 100%;
    font-size: .48rem;
    font-weight: bold;
    text-align: center;
  }
  .date-wrapper {
    position: absolute;
    top: 0;
    right: .2rem;
    font-size: .32rem;
    .calendar {
      color: green;
    }
  }
}
</style>



