<template>
  <div class="wrapper">
    <div class="circle" @click="handelNewTodo">
      <span class="fa fa-plus add"></span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeFooter',
  components: {},
  props: {},
  data () {
    return {
    }
  },
  watch: {},
  computed: {},
  methods: {
    handelNewTodo () {
      this.$router.push('/NewTodo')
    }
  },
  created () {},
  mounted () {}
}
</script>
<style lang="scss" scoped>
.wrapper {
  position: fixed;
  bottom: .3rem;
  right: 1.4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  .circle {
    width: 1rem;
    height: 1rem;
    line-height: 1rem;
    text-align: center;
    background: #59b159;
    border-radius: .5rem;
    .add {
      font-size: 1rem;
      color: #FFF;
    }
  }
}
</style>



